@import '../../assets/styles/variable';
@import '../../assets/styles/cosmetic';
@import '../../assets/styles/grid';

.billig-section {
    .billing-section-row {
        text-align: left;
        padding-top: 40px;
        color: $app-header;
        font-family: ProximaNova-Semibold;
        color: $app-header;
        margin-bottom: 45px;
        .billing-info {
            .header {
                font-size: 32px;
            }
            .sub-header {
                font-size: 16px;
                color: $sub-header;
                font-family: ProximaNova-Regular;
                &.desc {
                    padding-top: 5px;
                    font-size: 14px;
                }
            }
            .content-section {
                display: flex;
                .checkmark {
                    height: 15px;
                    width: 15px;
                    margin-top: 4px;
                }
            }
            .title {
                font-size: 18px;
                color: $desc-font;
                line-height: 134%;
            }
        }
        .customer-info {
            .set-max-width {
                max-width: 370px;
            }
            .header {
                font-family: ProximaNova-Light;
                font-size: 26px;                
                line-height: 120%
            }
            .sub-header {
                font-family: ProximaNova-Regular;
                font-size: 16px;
                color: $sub-header;
                line-height: 150%;
                border-bottom: 1px solid;
                &.url {
                    border: none;
                    color: $url-font;
                    font-style: bold;
                }
            }
            .client-section {
                display: flex;
                .logo {
                    height: 40px;
                    width: 40px;
                }
            }
        }
    }
}